import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import { useLocation } from "react-router-dom";

const Nav = () => {
  const { pathname } = useLocation();
  return (
    <NavBackground>
      <StyledNav>
        <Link id="logo" to="/">
          Chaker Hassani.
        </Link>
        <ul>
          <li>
            <Link to="/">1. About Us</Link>
            <motion.div
              transition={{ duration: 0.75 }}
              initial={{ width: "0%" }}
              animate={{ width: pathname === "/" ? "45%" : "10%" }}
              className="line"
            ></motion.div>
          </li>
          <li>
            <Link to="/work">2. My Work</Link>
            <motion.div
              transition={{ duration: 0.75 }}
              initial={{ width: "0%" }}
              animate={{ width: pathname === "/work" ? "45%" : "10%" }}
              className="line"
            ></motion.div>
          </li>
          <li>
            <Link to="/contact">3. Contact Us</Link>
            <motion.div
              transition={{ duration: 0.75 }}
              initial={{ width: "0%" }}
              animate={{ width: pathname === "/contact" ? "45%" : "10%" }}
              className="line"
            ></motion.div>
          </li>
        </ul>
      </StyledNav>
    </NavBackground>
  );
};

const NavBackground = styled.div`
  background: #282828;
  position: sticky;
  top: 0;
  z-index: 10;
`;

const StyledNav = styled.nav`
  min-height: 10vh;
  width: 85%;
  display: flex;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  color: white;

  a {
    color: white;
    text-decoration: none;
  }
  ul {
    display: flex;
    list-style: none;
  }
  #logo {
    font-size: 1.8rem;
    font-family: "Lobster", cursive;
    font-weight: lighter;
  }
  li {
    padding-left: 10rem;
    position: relative;
  }
  .line {
    height: 0.2rem;
    background: #0077be;
    position: absolute;
    bottom: -50%;
    left: 60%;
  }

  @media (max-width: 1300px) {
    width: 90%;
    li {
      padding-left: 8rem;
    }
  }

  @media (max-width: 1050px) {
    width: 95%;
    li {
      padding-left: 6rem;
    }
  }

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    padding: 2rem 0;
    #logo {
      margin-bottom: 1rem;
    }
    ul {
      padding: 0;
      justify-content: space-around;
      width: 100%;
    }
    li {
      padding: 0;
    }
    .line {
      left: 50%;
    }
  }
`;

export default Nav;